/* Welcome Section */
* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

html,
body {
  font-family: "Montserrat", sans-serif;
}

/* NavBar */

#navbar {
  /* background: linear-gradient(
    91.01deg,
    #072d66 -61.06%,
    #516c94 103.48%,
    #fefefe 112.06%
  ); */

  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
}

#navbar ul {
  list-style: none;
}

#navbar a {
  text-decoration: none;
  color: white;
}
.nav-link {
  position: relative;
  transition: all 0.2s ease-in-out;
}
.nav-link:hover {
  transform: scale(1.1);
}

#welcome-section {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(123.74deg, #00245a 0.68%, #d15555 122.89%);
  /* background: linear-gradient(125.16deg, #5a46d6 0.68%, #ff8585 89.72%); */
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.welcome-title {
  margin-bottom: 1.4rem;
}
.welcome-text {
  text-transform: uppercase;
  position: relative;
  border: none;
  border-radius: 50%;
}

.welcome-text:hover {
  box-shadow: 0 0 0 0 rgba(233, 30, 99, 1);

  animation: myflash 0.55s cubic-bezier(0.66, 0, 0, 1);
}

@keyframes myflash {
  to {
    box-shadow: 0 0 0 40px rgba(232, 76, 61, 0);
  }
}
