/* Project */

.proj-container {
  background: linear-gradient(
    171.17deg,
    #36caea 8.97%,
    rgba(201, 41, 99, 0.38) 113.04%
  );
  box-shadow: 10px 10px 6px rgba(202, 161, 190, 0.29);
  width: 100vw;
  min-height: 100vh;
}

/* Card */

.project-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 4rem;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}

.card {
  margin-top: 10em;
  width: 300px;
  transition: 0.2s;
}
.card:hover {
  transform: scale3d(1.1, 1.1, 1.1);
  transition: 0.25s;
}

.card-header {
  background: orange;
}

.custom-btn {
  display: none;
}
.test {
  color: black;
}

/* social links styling */

.social-media {
  margin-top: 5em;
  /* background: linear-gradient(123.74deg, #00245a 0.68%, #d15555 122.89%); */
  /* box-shadow: 8px 8px 6px rgba(66, 74, 191, 0.4); */
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  height: 20vh;
}

#profile-link > svg {
  color: black;
  font-size: 60px;
}

#profile-link svg:hover {
  animation: pulse 0.25s;
}

@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.2, 1.2, 1.2);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

/* footer styles */

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(360deg, #050505 -16.62%, #77808d 238.29%);
  height: 20vh;
}
footer p {
  color: white;
}

/* media queries */

@media only screen and (max-width: 1000px) {
  .project-wrapper {
    grid-template-columns: 1fr;
  }
}
